import React, { Component } from "react";
import Footer from "../Common/Footer";
import { Link } from "react-router-dom";

class Services extends Component {
  render() {
    return (
      <div>
        <section id="service" className="section-padding">
          <div className="container">
            <div className="row">
              <div className="col-md-4 col-sm-4">
                <h2 className="ser-title">Services Offered</h2>
                <hr className="botm-line" />
                <p>
                  I create websites at affordable prices. I am ready for hire
                  and currently available from 8a.m to 11:59pm. I work with
                  backend frameworks like Django and Flask. I also currenlty
                  work with front-end frameworks like React.js. I do data
                  analyis using SPSS and R. I am a founder of Spazza, a food
                  delivery platform that is yet to be launched to the market. I
                  also tutor Python programming course at Swahilibox and I am
                  free to coach you! Just contact me if you are interested.
                </p>
              </div>
              <div className="col-md-4 col-sm-4">
                <div className="service-info">
                  <div className="icon">
                    <i className="fa fa-user-times"></i>
                  </div>
                  <div className="icon-info">
                    <h4>Remote Work</h4>
                    <p>I work remotely as a Software Engineer with Libryo.</p>
                  </div>
                </div>
                <div className="service-info">
                  <div className="icon">
                    <i className="fa fa-building"></i>
                  </div>
                  <div className="icon-info">
                    <h4>Front-End(UI)</h4>
                    <p>
                      I work with React and Bootrap to create user friendly
                      interfaces for my clients. So if yo are interested in nice
                      looking static website then you are my target client.
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-4 col-sm-4">
                <div className="service-info">
                  <div className="icon">
                    <i className="fa fa-database"></i>
                  </div>
                  <div className="icon-info">
                    <h4>Back-end</h4>
                    <p>
                      I also offer backend support systems like: time management
                      system, database management system and back-end support
                      like authentication and authorization. The frameworks I
                      use on backend are mainly Django and Flask.
                    </p>
                  </div>
                </div>
                <div className="service-info">
                  <div className="icon">
                    <i className="fa fa-users"></i>
                  </div>
                  <div className="icon-info">
                    <h4>Data Analysis</h4>
                    <p>
                      I have expertise in data analysis softwares like SPSS and
                      R and have a long working expertise in this field.I do
                      data analysis techniques like regression, correlation,
                      T-Tests, ANOVA, MANOVA etc.I also do descriptive analyis
                      with SPSS and advanced data analyis with Ms Excel using
                      pivotal tables and slices.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="overlay-detail text-center">
            <Link to="/testimonial">
              <i className="fa fa-angle-down"></i>
            </Link>
          </div>
        </section>
        <Footer />
      </div>
    );
  }
}
export default Services;
