import React, {Component} from 'react'
import {Link} from 'react-router-dom'

class Header extends Component {
    render(){
        return (
                <div className="container">
                    <div className="row">
                        <div className="banner-info">
                            <div className="banner-logo text-center">
                                <img src={this.props.image} alt="My Logo" className="img-responsive" style={{ width: "800px", height: "350px", marginTop: "-16px" }}/>
                            </div>
                            <div className="banner-text text-center">
                                <h1 className="white">{this.props.title}</h1>
                                <p>{this.props.subtitle}</p>
                                <Link to="/contact" className="btn btn-appoint">{this.props.link}</Link>
                            </div>
                            <div className="overlay-detail text-center">
                                <Link to="/about"><i className="fa fa-angle-down"></i></Link>
                            </div>		
                        </div>
                    </div>
                </div>
            
        )
    }
}
export default Header;