import React, { Component } from "react";
import { Link } from "react-router-dom";
import Footer from "../Common/Footer";

class Testimonial extends Component {
  render() {
    return (
      <div>
        <section id="testimonial" className="section-padding">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <h2 className="ser-title">Other Achievements</h2>
                <hr className="botm-line" />
              </div>
              <div className="col-md-4 col-sm-4">
                <div className="testi-details">
                  <p>
                    Co-Founder of a starter "Baharini Fresh" app that won the
                    first position at Mombasa Festival Week in 2018 that was
                    sponsored by Cellulant and Mula .
                  </p>
                </div>
                <div className="testi-info">
                  <Link to="/">
                    <img
                      src="img/myprofile.jpg"
                      alt=""
                      className="img-responsive"
                    />
                  </Link>
                  <h3>
                    Isaac<span>Msa</span>
                  </h3>
                </div>
              </div>
              <div className="col-md-4 col-sm-4">
                <div className="testi-details">
                  <p>
                    Completed Mobile Web Application course sponsored by Google,
                    Andela and Pluralsight
                  </p>
                </div>
                <div className="testi-info">
                  <Link to="/">
                    <img
                      src="img/myprofile.jpg"
                      alt=""
                      className="img-responsive"
                    />
                  </Link>
                  <h3>
                    Isaac<span>Msa</span>
                  </h3>
                </div>
              </div>
              <div className="col-md-4 col-sm-4">
                <div className="testi-details">
                  <p>
                    Completed a Front-End Development Course with Andela in 2018
                  </p>
                </div>
                <div className="testi-info">
                  <Link to="/">
                    <img
                      src="img/myprofile.jpg"
                      alt=""
                      className="img-responsive"
                    />
                  </Link>
                  <h3>
                    Isaac<span>Msa</span>
                  </h3>
                </div>
              </div>
            </div>
          </div>
          <div className="overlay-detail text-center">
            <Link to="/contact">
              <i className="fa fa-angle-down"></i>
            </Link>
          </div>
        </section>
        <Footer />
      </div>
    );
  }
}

export default Testimonial;
