import React, { Component } from "react";
// import {Link} from 'react-router-dom';
import Header from "../Common/Header";
import image from "../assets/img/myprofile.jpg";

class Index extends Component {
  render() {
    return (
      <div>
        <Header
          title="Software Engineer"
          subtitle="I am a fullstack Web developer and I specilaize in Python and Javascript. I am proficient with Python's Django and Flask Frameworks; and Javascript's React framework"
          link="Contact Me"
          image={image}
        />
      </div>
    );
  }
}
export default Index;
