import React, { Component } from "react";
import Footer from "../Common/Footer";
import { withFormik } from "formik";
import { Link } from "react-router-dom";
import "../../css/main.css";

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      email: "",
      subject: "",
      message: ""
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  render() {
    const {
      values,
      touched,
      errors,
      handleBlur,
      handleChange,
      handleSubmit
    } = this.props;
    return (
      <div>
        <section id="contact" className="section-padding">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <h2 className="title">Contact me</h2>
                <hr className="botm-line" />
              </div>
              <div className="col-md-4 col-sm-4">
                <h3>Contact Info</h3>
                <div className="space"></div>
                <p>
                  <i className="fa fa-map-marker fa-fw pull-left fa-2x"></i>
                  L/LUNGA ROAD 96663-80110
                  <br />
                  MOMBASA
                </p>
                <div className="space"></div>
                <p style={{ color: "#0cb8b6" }}>
                  <i className="fa fa-envelope-o fa-fw pull-left fa-2x"></i>
                  <a href="mailto:isaacnyakoi1@gmail.com">
                    isaacnyakoi1@gmail.com
                  </a>
                </p>
                <div className="space"></div>
                <p>
                  <i className="fa fa-phone fa-fw pull-left fa-2x"></i>+254 716
                  133 905
                  <br />
                  +254 773 833 489
                </p>
              </div>
              <div className="col-md-8 col-sm-8 marb20">
                <div className="contact-info">
                  <h3 className="cnt-ttl">Having Any Query, Email me</h3>
                  <div className="space"></div>
                  <div id="sendmessage">
                    Your message has been sent. Thank you!
                  </div>
                  <div id="errormessage"></div>
                  <form className="contactForm" onSubmit={handleSubmit}>
                    <div className="form-group">
                      <input
                        type="text"
                        name="name"
                        required="required"
                        className="form-control br-radius-zero"
                        id="name"
                        placeholder="Your Name"
                        data-rule="minlen:4"
                        data-msg="Please enter at least 4 chars"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.name}
                      />
                      {errors.name && touched.name && (
                        <div id="feedback">{errors.name}</div>
                      )}
                    </div>
                    <div className="form-group">
                      <input
                        type="email"
                        className="form-control br-radius-zero"
                        name="email"
                        id="email"
                        placeholder="Your Email"
                        data-rule="email"
                        data-msg="Please enter a valid email"
                        required="required"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.email}
                      />
                      {errors.email && touched.email && (
                        <div id="feedback">{errors.email}</div>
                      )}
                    </div>
                    <div className="form-group">
                      <input
                        type="text"
                        required="required"
                        className="form-control br-radius-zero"
                        name="subject"
                        id="subject"
                        placeholder="Subject"
                        data-rule="minlen:4"
                        data-msg="Please enter at least 8 chars of subject"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.subject}
                      />
                      {errors.subject && touched.subject && (
                        <div id="feedback">{errors.subject}</div>
                      )}
                    </div>
                    <div className="form-group">
                      <textarea
                        required="required"
                        className="form-control br-radius-zero"
                        name="message"
                        rows="5"
                        data-rule="required"
                        data-msg="Please write something for us"
                        placeholder="Message"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.message}
                      />
                      {errors.message && touched.message && (
                        <div id="feedback">{errors.message}</div>
                      )}
                    </div>

                    <div className="form-group">
                      <button type="submit" className="btn btn-form">
                        Send Message
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <div className="overlay-detail text-center">
            <Link to="/">
              <i className="fa fa-angle-down"></i>
            </Link>
          </div>
        </section>
        <div>
          <Footer />
        </div>
      </div>
    );
  }
}

// export default Contact;
export default withFormik({
  mapPropsToValues: () => ({
    name: "",
    email: "",
    subject: "",
    message: ""
  }),
  validate: values => {
    const errors = {};
    if (!values.name) {
      errors.name = "Name is required";
    } else if (!isNaN(values.name)) {
      errors.name = "Only text is required";
    } else if (values.name.length > 20 || values.name.length < 3) {
      errors.name = "Must not be more than 20 and not less than 3";
    }

    if (!values.email) {
      errors.email = "Email is required";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
      errors.email = "Email is invalid";
    }

    if (!values.subject) {
      errors.subject = "Subject is required";
    } else if (!isNaN(values.subject)) {
      errors.subject = "Only text is required";
    } else if (values.subject.length > 20 || values.subject.length < 3) {
      errors.subject = "Must not be more than 20 and not less than 3";
    }

    if (!values.message) {
      errors.message = "Message is required";
    } else if (!isNaN(values.message)) {
      errors.message = "Only text is required";
    } else if (values.message.length > 100 || values.message.length < 3) {
      errors.message = "Must not be more than 100 and not less than 3";
    }

    return errors;
  },
  handleSubmit: (values, { setSubmitting, resetForm }) => {
    const templateId = "template_2ow7qlj";
    const serviceId = "service_758e3cj";
    function sendFeedback(serviceId, templateId, variables) {
      window.emailjs
        .send(serviceId, templateId, variables)
        .then(res => {
          console.log("Email successfully sent!");
          // alert("Your form has been submitted successfully");
        })
        // Handle errors here however you like, or use a React error boundary
        .catch(err =>
          console.error(
            "Oh well, you failed. Here some thoughts on the error that occured:",
            err
          )
        );
    }
    setTimeout(() => {
      sendFeedback(serviceId, templateId, {
        name: values.name,
        email: values.email,
        subject: values.subject,
        message: values.message
      });
      // alert(JSON.stringify(values, null, 2));
      setSubmitting(false);
      resetForm({ values: "" });
      const feedback = document.getElementById("sendmessage");
      feedback.style.display = "block";
    }, 2000);

    setTimeout(() => {
      const feedback = document.getElementById("sendmessage");
      feedback.style.display = "none";
    }, 6000);
  }
})(Contact);
